/* íì ì»¨íì´ë */
.popup-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* íì ë¤ (Dim) */
    .popup-dim {
        height: 100%;
        background: rgba(0, 0, 0, 0.1);
    }
}

/* íì ìì­ */
.popup {
    width: 870px;
    min-height: 500px;
    background: #fff;
    box-shadow: 0 3px 10px rgba(0,0,0,0.3);
    outline: 0; 
    z-index: 10000;
    &.is-center {
        @include wh-center($position: absolute);
    }
    /* íì í¤ëë¼ì¸ */
    .popup-headline {
        height: 50px;
        line-height: 50px;
        padding: 0 30px;
        background: url($popup-title) center no-repeat;
        @extend %clearfix;
        h4 {
            float: left;
            font-size: 18px;
            color: #fff;
            letter-spacing: -0.04em;
        }
        .popup-close {
            float: right;
            width: 21px;
            height: 21px;
            margin-top: 15px;
            @extend %text-hidden;
            background: url($btn-gnb-close) center no-repeat;
        }
    }
    /* íì ì½íì¸  */
    .popup-content {
        padding: 20px 30px 30px 30px;
        .btn-tl-set {
            text-align: left;
            margin-bottom: 10px;
        }
        .btn-tr-set {
            text-align: right;
            margin-bottom: 10px;
        }
        .btn-cl-set {
            text-align: left;
            margin-top: 10px;
            margin-bottom: 10px;
        }
        .btn-cr-set {
            text-align: right;
            margin-top: 10px;
            margin-bottom: 10px;
        }
        .btn-cc-set {
            text-align: center;
            margin-top: 10px;
            margin-bottom: 10px;
        }
        .popup-btn-set {
            width: 100%;
            text-align: center;
            margin-top: 30px;
            .popup-close {
                @include btn-size ($dp: inline-block, $w: 66px);
                @include btn-type-1 ($fz: 14px, $color: #333, $bd: $skyblue, $bg: $skyblue);
            }
        }
    }
}