/* =======================================*/
/* Layout Common Style*/
/* =======================================*/
body {
    min-width: 1200px;
    * {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        word-wrap: break-word;
    }
    .wrap {
      width: 1248px;
      padding: 0 24px;
      margin: 0 auto;
    }
}

/* =======================================*/
/* ìì´ì½ ê³µíµ ì¤íì¼*/
/* =======================================*/
.icon-hwp {
    position: relative;
    padding-left: 20px;
    &:before {
        position: absolute;
        top: 2px;
        left: 0;
        display: inline-block;
        width: 14px;
        height: 16px;
        content: "";
        background: url($icon-hwp) no-repeat;
    }
}
.icon-doc {
    @extend .icon-hwp;
    &:before {
        background: url($icon-doc) no-repeat;
    }
}
.icon-pdf {
    @extend .icon-hwp;
    &:before {
        background: url($icon-pdf) no-repeat;
    }
}
.icon-jpg {
    @extend .icon-hwp;
    &:before {
        background: url($icon-jpg) no-repeat;
    }
}

