/* =======================================*/
/* Skip Navigation ì¤ì */
/* =======================================*/
.skip-link {
    position: absolute;
    top: -33px;
    display: inline-block;
    width: 100%;
    height: 28px;
    line-height: 28px;
    text-align: center;
    background: #555;
    &:hover, &:focus, &:active {
        top: 0;
        font-weight: bold;
        color: #eee;
    }
}

/* =======================================*/
/* Header : Top menu*/
/* =======================================*/
.header {
    @extend %clearfix;
    .top-menu {
        height: 28px;
        line-height: 28px;
        border-bottom: 1px solid #ebebeb;
        .wrap {
            text-align: right;
            a {
                font-size: 12px;
                color: #757575;
                &:after {
                    display: inline-block;
                    content: "/";
                    padding: 0 6px 0 10px;
                }
                &:last-child {
                    &:after {
                        content: "";
                        padding: 0;
                    }
                }
            }
        }
    }
}

 /* =======================================*/
 /* Visual Banner : ìë¨ ê³µíµ ë°°ë*/
 /* =======================================*/
.sub-visual {
    width: 100%;
