/* =======================================*/
/* Container : ì»¨íì´ë ìì­*/
/* =======================================*/
#container {
    display: block;
    min-height: 670px;
    padding-top: 50px;
    padding-bottom: 30px;
    @extend %clearfix;
    /* Content ìì­ */
    .content-area {
        float: left;
        margin-left: 24px;
        width: 950px;
        .headline {
            height: 58px;
            line-height: 58px;
            /* Sub Page : Title ê³µíµ */
            .sub-title {
                font-weight: bold;
                letter-spacing: -0.03em;
                background: url($sub-title) 0 50% no-repeat;
                padding-left: 25px;
                float: left;
            }
            /* Sub Page : Location ê³µíµ */
            .site-location {
                float: right;
                a {
                    font-size: 12px;
                    color: #666;
                    letter-spacing: -0.03em;
                    img {
                        vertical-align: text-top;
                    }
                    &:before {
                        display: inline-block;
                        font-family: 'FontAwesome';
                        font-size: 16px;
                        content: "\f105";
                        padding: 0 4px 0 2px;
                    }
                    &:first-child {
                        &:before {
                            content:"";
                        }
                    }
                    &:last-child {
                        color: #333;
                    }
                }
            }
        }
    }
}

