/* =======================================*/
/* Footer : ì¬ì´í¸ ì ë³´*/
/* =======================================*/
.footer {
    width: 100%;
    height: 170px;
    font-size: 12px;
    color: #8f8f8f;
    background: #06111a;
    .site-info {
        text-align: center;
        padding-top: 45px;
        padding-bottom: 25px;
        a {
            display: inline-block;
            vertical-align: middle;
            span {
                display: block;
                padding: 0 18px;
                height: 13px;
                line-height: 13px;
                border-right: 1px solid #808080;
            }
            &:last-child {
                span {
                    border-right: 0;
                }
            }
        }
    }
    p {
        text-align: center;
        line-height: 1.8em;
        .copyright {
            font-size: 12px;
        }
    }
}

