/* íì´ë¸ ì¤íì¼*/
/* =======================================*/
.download {
    [class^="icon-"] {
        display: block;
        margin-bottom: 5px;
        color: #003761;
        font-weight: bold;
        &:last-child {
            margin-bottom: 0;
        }
