.table-type-01 {
    width: 100%;
    thead {
        border-top: 2px solid #45494d;
        th {
            border-right: 1px solid #d1d1d1;
            border-bottom: 1px solid #c2c2c2;
        }
    }
    tbody {
        th, td {
            border-right: 1px solid #d1d1d1;
            border-bottom: 1px solid #d1d1d1;
        }
    }
    tr {
        th {
            background: #f0f3f5;
            color: #2e3a42;
        }
        th, td {
            height: 38px;
            text-align: center;
            vertical-align: middle;
            padding: 0 20px;
            a {
                @include ellipsis(1);
            }
            &:first-child {
                padding-left: 6px;
                padding-right: 6px;
            }
            &:last-child {
                border-right: 0;
            }
        }
    }
}
.table-type-02 {
    width: 100%;
    tr {
        th, td {
            height: 42px;
            text-align: left;
            vertical-align: middle;
            padding: 6px 20px;
            border: 1px solid #d1d1d1;
            a {
                @include ellipsis(1);
            }
        }
        th {
            background: #f0f3f5;
            color: #2e3a42;
            border-right: 0;
        }
        .lines {
            padding-top: 10px;
            padding-bottom: 10px;
        }
    }
}
.table-li {
    display: table;
    width: 100%;
    border: 1px solid #d1d1d1;
    li {
        display: table-cell;
        vertical-align: middle;
        height: 42px;
        padding: 0 20px;
        border-right: 1px solid #d1d1d1;
        &.title {
            font-weight: bold;
            color: #2e3a42;
            background: #f0f3f5;
        }
        &.lines {
            padding-top: 6px;
            padding-bottom: 6px;
        }
        &:last-child {
            border-right: 0;
        }
    }
}
.table-dt-dd {
    width: 100%;
    border: 1px solid #d1d1d1;
    dt, dd {
        padding: 0 20px;
        border-bottom: 1px solid #d1d1d1;
        &:last-child {
            border-bottom: 0;
        }
    }
    dt {
        height: 42px;
        line-height: 42px;
        font-weight: bold;
        text-align: center;
        color: #2e3a42;
        background: #f0f3f5;
    }
    .lines {
        padding-top: 10px;
        padding-bottom: 10px;
    }
}
.table-btn-set {
    margin-top: 30px;
    text-align: center;
    [class^="btn-"] {
        margin-right: 3px;
        &:last-child {
            margin-right: 0;
        }
    }
}