.qna-content {
    thead {
        th {
            &:first-child {
                width: 54px;
            }
            &:nth-child(2) {
                width: 140px;
            }
            &:nth-child(3) {
                width: 341px;
            }
            &:nth-child(4) {
                width: 110px;
            }
            &:nth-child(5) {
                width: 140px;
            }
            &:last-child {
                width: 140px;
            }
        }
    }
    tbody {
        td {
            &:nth-child(3) {
                text-align: left;
            }
        }
    }
}

